.task-delegation-panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

.task-delegation-panel > div {
  background: #1e1e1e;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.4);
  color: white;
  max-width: 95vw;
  max-height: 95vh;
  overflow-y: auto;
  width: 1000px;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #333;
  background: linear-gradient(135deg, #2d2d2d, #1e1e1e);
  position: relative;
}

/* Back to Terminal Button */
.back-to-terminal-button {
  background: linear-gradient(135deg, #4a5568, #2d3748);
  border: 1px solid #4a5568;
  border-radius: 6px;
  color: white;
  font-size: 14px;
  font-weight: 500;
  padding: 8px 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 4px;
  transition: all 0.2s;
  position: absolute;
  left: 24px;
  z-index: 1;
}

.back-to-terminal-button:hover {
  background: linear-gradient(135deg, #5a6578, #3d4758);
  border-color: #5a6578;
  transform: translateX(-2px);
}

.back-to-terminal-button:active {
  transform: translateX(-1px);
}

.panel-header h2 {
  margin: 0 auto;
  font-size: 24px;
  font-weight: 600;
  color: #ffffff;
  text-align: center;
  flex: 1;
}

.close-button {
  background: #ff4444;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 18px;
  width: 32px;
  height: 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s;
}

.close-button:hover {
  background: #ff6666;
}

/* Mode Indicator Styles */
.mode-indicator {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
}

.mode-badge {
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
}

.mode-badge.real {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
}

.mode-badge.demo {
  background: linear-gradient(135deg, #6366f1, #4f46e5);
  color: white;
  box-shadow: 0 2px 8px rgba(99, 102, 241, 0.3);
}

/* Task Setup Styles */
.task-setup {
  padding: 24px;
}

.task-input-section {
  margin-bottom: 32px;
}

.task-input-section h3 {
  margin: 0 0 16px 0;
  font-size: 18px;
  color: #ffffff;
}

.task-input {
  width: 100%;
  background: #2d2d2d;
  border: 2px solid #444;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  padding: 12px;
  resize: vertical;
  min-height: 100px;
  font-family: inherit;
  transition: border-color 0.2s;
}

.task-input:focus {
  outline: none;
  border-color: #4CAF50;
}

.task-input::placeholder {
  color: #888;
}

.context-detection {
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 8px;
}

.context-label {
  color: #aaa;
  font-size: 12px;
}

.context-tag {
  background: #4CAF50;
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 11px;
  font-weight: 500;
}

/* Preset Selection Styles */
.preset-selection h3 {
  margin: 0 0 16px 0;
  font-size: 18px;
  color: #ffffff;
}

.presets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 16px;
  margin-bottom: 32px;
}

.preset-card {
  background: #2d2d2d;
  border: 2px solid #444;
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;
  transition: all 0.2s;
}

.preset-card:hover {
  border-color: #666;
  background: #333;
}

.preset-card.selected {
  border-color: #4CAF50;
  background: #2d4a2d;
}

.preset-icon {
  font-size: 32px;
  margin-bottom: 8px;
}

.preset-name {
  font-size: 16px;
  font-weight: 600;
  color: white;
  margin-bottom: 4px;
}

.preset-description {
  font-size: 12px;
  color: #aaa;
  margin-bottom: 12px;
  line-height: 1.4;
}

.preset-agents {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.agent-badge {
  background: #444;
  color: #ccc;
  padding: 3px 8px;
  border-radius: 10px;
  font-size: 10px;
  font-weight: 500;
}

/* Start Button */
.start-section {
  text-align: center;
}

.execution-note {
  margin-top: 8px;
  font-size: 11px;
  color: #10b981;
  font-style: italic;
}

.start-delegation-button {
  background: linear-gradient(135deg, #4CAF50, #45a049);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  padding: 14px 32px;
  cursor: pointer;
  transition: all 0.2s;
  min-width: 200px;
}

.start-delegation-button:hover:not(:disabled) {
  background: linear-gradient(135deg, #45a049, #4CAF50);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);
}

.start-delegation-button:disabled {
  background: #666;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Active Session Styles */
.active-session {
  padding: 24px;
}

.session-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
}

.session-info h3 {
  margin: 0 0 8px 0;
  font-size: 18px;
  color: white;
  line-height: 1.3;
}

.session-meta {
  display: flex;
  gap: 16px;
  font-size: 12px;
  color: #aaa;
}

.stop-button {
  background: #ff4444;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 14px;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.stop-button:hover {
  background: #ff6666;
}

/* Progress Bar */
.progress-bar-container {
  background: #333;
  border-radius: 4px;
  height: 8px;
  margin-bottom: 24px;
  overflow: hidden;
}

.progress-bar {
  background: linear-gradient(90deg, #4CAF50, #45a049);
  height: 100%;
  transition: width 0.3s ease;
  border-radius: 4px;
}

/* Agents Grid */
.agents-container h4 {
  margin: 0 0 16px 0;
  font-size: 16px;
  color: white;
}

.agents-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 16px;
  margin-bottom: 24px;
}

.agent-card {
  background: #2d2d2d;
  border: 1px solid #444;
  border-radius: 8px;
  padding: 16px;
}

.agent-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.agent-header h5 {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  color: white;
}

.status-dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  flex-shrink: 0;
}

.agent-specialization {
  font-size: 12px;
  color: #aaa;
  margin-bottom: 12px;
}

.agent-progress {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 12px;
}

.progress-container {
  flex: 1;
  background: #444;
  border-radius: 4px;
  height: 6px;
  overflow: hidden;
}

.progress-fill {
  background: #4CAF50;
  height: 100%;
  transition: width 0.3s ease;
  border-radius: 4px;
}

.progress-text {
  font-size: 11px;
  color: #aaa;
  min-width: 30px;
}

.agent-task {
  margin-bottom: 8px;
  min-height: 32px;
}

.current-task {
  font-size: 12px;
  color: white;
  font-style: italic;
}

.idle-text {
  font-size: 12px;
  color: #666;
  font-style: italic;
}

.agent-stats {
  font-size: 11px;
  color: #888;
}

/* Completion Summary */
.completion-summary {
  background: #2d4a2d;
  border: 1px solid #4CAF50;
  border-radius: 8px;
  padding: 16px;
  text-align: center;
}

.completion-summary h4 {
  margin: 0 0 8px 0;
  color: #4CAF50;
  font-size: 16px;
}

.completion-summary p {
  margin: 0;
  color: #aaa;
  font-size: 14px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .task-delegation-panel > div {
    width: 95vw;
    margin: 20px;
  }
  
  .presets-grid {
    grid-template-columns: 1fr;
  }
  
  .agents-grid {
    grid-template-columns: 1fr;
  }
  
  .session-header {
    flex-direction: column;
    gap: 16px;
    align-items: flex-start;
  }
  
  .session-meta {
    flex-direction: column;
    gap: 4px;
  }
}